* {
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

h3 {
  margin: 20px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
  
}
.card img{
  max-height: 160px;
}